<template>
<transition enter-active-class="animate__animated animate__bounceIn" leave-active-class="animate__animated animate__fadeOutDown">
        <div class="player" v-if="$root.musicStore.musicID" v-show="$root.playBar">
            <audio 
            :src="`https:music.163.com/song/media/outer/url?id=${$root.musicStore.musicID}.mp3`"
            autoplay
            muted="muted"
            @timeupdate="timeUpdate"
            @pause="pause"
            @play="play"
            @ended="nextSong"
            ref="audio"
            ></audio>
            <!-- 播放栏 -->
            <div class="playBar" v-if="$root.musicStore.musicID">
                <i ref="randImg">
                    <img :src="this.$root.musicStore.currentMusic.picUrl+'?imageView=1&type=webp&thumbnail=369x0'" alt="" v-if="this.$root.musicStore.currentMusic.picUrl"/>
                    <img src="../assets/imgs/musicbg.gif"  alt="" v-else />
                </i>
                <i @click="isFullPage=true" v-html="$root.musicStore.currentMusic.name"></i>
                <i>
                    <canvas height="100" ref="processBtn" width="100"></canvas>
                </i>
                <div class="highlight" @click="chanPlay">
                    <img src="../assets/imgs/pause.png" alt="" v-if="isPlay">
                    <img src="../assets/imgs/play.png" alt="" v-else>
                </div>
            </div>
            <!-- 播放页 -->
            <div class="fullPage" v-show="isFullPage">
                <!-- 背景图 -->
                <div class="bg">
                    <img :src="this.$root.musicStore.currentMusic.picUrl+'?imageView=1&type=webp&thumbnail=369x0'" alt="" v-if="this.$root.musicStore.currentMusic.picUrl" />
                    <img src="../assets/imgs/PlayBG.jpg" style="filter='brightness(60%)'" alt="" v-else />
                </div>
                <!-- 返回键 歌单 -->
                <div class="top">
                <span class="clock" @click="isFullPage=false,isPlayList=false"></span>
                <h3 v-html="$root.musicStore.currentMusic.name+artists" class="lyricTitle"></h3>
                <span @click="isPlayList=true" class="iconfont icon-songlist" ></span>
                </div>
                <!-- 图片-->
                <div ref="songImg" class="songImg">
                    <div @click="islyricFull=true">
                    <img :src="this.$root.musicStore.currentMusic.picUrl+'?imageView=1&type=webp&thumbnail=369x0'" alt="" v-if="this.$root.musicStore.currentMusic.picUrl"/>
                    <img src="../assets/imgs/musicbg.gif"  alt="" v-else />
                    </div>
                </div>
                <!-- 歌词 -->
                <div class="lyric" :class="[islyricFull ? 'lyricFull' : '']" v-if="lyric" ref="lyric" @click="islyricFull=false">
                    <h3 class="lyricTitle" v-html="$root.musicStore.currentMusic.name+artists"></h3>
                    <div>
                        <ul :style="{transform:`translateY(-${moveTop}px)`}">
                            <li ref="lyricLi" v-for="(item,index) in lyric" 
                            :key="index" :class="{active:currentIndex==index}"
                            >{{item.lyric}}</li>
                        </ul>
                    </div>
                </div>
                <!-- 如果没找到歌词 -->
                <div class="nolyric" v-else>
                    <h3 class="lyricTitle" v-html="$root.musicStore.currentMusic.name+artists"></h3>
                    <p>抱歉！无法搜索到对应歌词！</p>
                </div>
                <!-- 播放进度条 -->
                <div class="propress_open">
                    <i>{{currentTime | formatTime}}</i>
                    <input type="range" min="0" :max="duration" :value="currentTime" @change="changeTime">
                    <i>{{duration | formatTime}}</i> 
                </div>
                <!-- 播放按钮 -->
                <div class="controlBar">
                    <span @click="playMode" class="iconfont icon-danquxunhuan" v-if="playmode==1"></span>
                    <span @click="playMode" class="iconfont icon-suijibofang" v-else-if="playmode==2"></span>
                    <span @click="playMode" class="iconfont icon-shunxubofang" v-else></span>
                    <span @click="prevSong" class="iconfont icon-shangyigeshangyiqu" ></span>
                    <span @click="chanPlay" :class="[isPlay ? 'iconfont icon-weibiaoti--' : 'iconfont icon-weibiaoti--1']" ></span>
                    <span @click="nextSong" class="iconfont icon-xiayigexiayiqu" ></span>
                    <span class="like iconfont" :class="[ likeState ? 'icon-icon_xihuan' : 'icon-xihuan-tianjia']" @click="like"></span>
                </div>
                <!-- 播放列表 -->
                <div class="playList" v-if="$root.musicStore.musicList" :class=" [ isPlayList ? 'active' : '' ]">
                    <p>播放列表<span class="iconfont icon-guanbi"  @click="isPlayList=false"></span></p>
                    <ul>
                        <li v-for="v in $root.musicStore.musicList" :key="v.id" v-html=" v.name" :style="{ color: v.id==$root.musicStore.musicID ? '#df3436' : ''}" @click="playMusic(v)"></li>
                    </ul>
                </div>
            </div>
        </div>
    </transition>
</template>

<script>
export default {
    name:"Play",
    data(){
        return{
            isPlay:true,
            pp:false,
            isFullPage:false,//返回页
            duration:0,
            currentTime:0,
            playBar:false,//播放栏
            lyric:null,//歌词
            moveTop:0,
            currentIndex:1,
            isPlayList:false,//歌单
            playmode:3, //随机 循环 顺序
            islyricFull:false, //歌词全屏
            artists:null, //当前歌词高亮
            likeState:null,//收藏
            playLists:[] //播放记录
        }
    },
    updated(){
        if(this.$root.musicStore.currentMusic.song){
            this.artists = "<p calss='artists' style='color:#ddd;font-size:12px;line-height:17px;padding-right:14px;margin-top:3px;'>&nbsp;&nbsp;&nbsp;&nbsp;"+this.$root.musicStore.currentMusic.song.artists[0].name+"</p>";
        }
        if(this.$root.musicStore.currentMusic.artists){
            this.artists = "<p calss='artists' style='color:#ddd;font-size:14px;line-height:17px;padding-right:14px;margin-top:3px;'>&nbsp;&nbsp;&nbsp;&nbsp;"+this.$root.musicStore.currentMusic.artists[0].name+"</p>";
        }
    },
    methods:{
        pauseThis(){
            this.$refs.audio.pause();
        },
        playMusic(item){ //播放音乐
            this.$root.musicStore.musicID=item.id;
            this.$root.musicStore.currentMusic=item;
        },
        changeSong(){//找到歌曲的ID信息
            let currentIndex=0;
            let musicList=this.$root.musicStore.musicList;
            if(this.playmode==2) return parseInt(musicList.length*Math.random());//随机播放
            for(let i in musicList){
                if(musicList[i].id === this.$root.musicStore.musicID){
                    currentIndex=i;
                    break;
                }
            }
            return currentIndex;
        },
        playMode(){ //数字切换
            this.playmode ++;
            this.playmode = this.playmode > 3 ? this.playmode = 1 : this.playmode;
            if(this.playmode == 1){ //判断是否单曲循环 单曲循环开启audio循环播放属性
                this.$refs.audio.loop = true;
            }else{
                this.$refs.audio.loop = false;
            }
        },
        prevSong(){//上一曲
            let currentIndex=this.changeSong();
            let musicList=this.$root.musicStore.musicList;
            if(this.playmode != 1) currentIndex--;
            if(currentIndex<0){ //如果上一曲下标为负数，上一曲是最后一曲
                currentIndex=musicList.length-1;
            }
            //改变当前播放音乐的信息
            this.$root.musicStore.musicID=musicList[currentIndex].id;//改变播放ID
            this.$root.musicStore.currentMusic=musicList[currentIndex];//改变当前音乐播放信息
        },
        nextSong(){//下一曲
            let currentIndex=this.changeSong();
            let musicList=this.$root.musicStore.musicList;
            if(this.playmode != 1) currentIndex++;
            if(currentIndex>=musicList.length){ //如果上一曲下标为负数，上一曲是最后一曲
                currentIndex=0;
            }
            //改变当前播放音乐的信息
            this.$root.musicStore.musicID=musicList[currentIndex].id;//改变播放ID
            this.$root.musicStore.currentMusic=musicList[currentIndex];//改变当前音乐播放信息
        },
        pause(event){ //暂停
            this.isPlay=true;
            this.chanPlay();
            this.timeUpdate(event);
        },
        play(event){ //播放
           this.isPlay=false;
           this.chanPlay(); //改变状态及动画
           this.timeUpdate(event);
        },
        changeTime(event){ 
            let time = event.target.value;
            this.currentTime=time;
            this.$refs.audio.currentTime=time;
        },
        chanPlay(){  //播放暂停
            if(this.isPlay){
                this.$refs.audio.pause();
                this.isPlay = false;
                this.$refs.randImg.style.animationPlayState = "paused";
                this.$refs.songImg.style.animationPlayState = "paused";
            }else{
                this.$refs.audio.play();
                this.isPlay = true;
                this.$root.playBar = true;
                this.$refs.randImg.style.animationPlayState = "running";
                this.$refs.songImg.style.animationPlayState = "running";
            }
        },
        timeUpdate(event){
            let audio = event.target;//获取audio元素
            let currentTime=audio.currentTime;//当前播放总时间
            let duration=audio.duration;//获取音频总时长
            this.currentTime=currentTime;
            this.duration=duration;
            this.drawCircle(currentTime,duration);
            if(this.lyric){
                    for(var i=0;i<this.lyric.length;i++){
                        if(i>=this.lyric.length-1){
                            this.currentIndex=i;
                            break;
                        }
                        if(this.lyric[i].time<=currentTime&&currentTime<this.lyric[i+1].time){
                            if(i>=2){
                                this.moveTop=this.$refs.lyricLi[i-2].offsetTop;
                            }else{
                                this.moveTop=0;
                            }
                            this.currentIndex=i;
                            break;
                        }
                    }
                }
        },
        drawCircle(currentTime,duration){
            let canvas = this.$refs.processBtn.getContext('2d');
            //绘制颜色渐变
            var gradient=canvas.createLinearGradient(0,0,170,0);
            gradient.addColorStop("0","magenta");
            gradient.addColorStop("0.5","blue");
            gradient.addColorStop("1.0","red");
            // 解决锯齿问题
            if (window.devicePixelRatio) {
                this.$refs.processBtn.style.width = "50px";
                this.$refs.processBtn.style.height = "50px";
                this.$refs.processBtn.height = 40 * window.devicePixelRatio;
                this.$refs.processBtn.width = 40 * window.devicePixelRatio;
                canvas.scale(window.devicePixelRatio, window.devicePixelRatio);
            }
            // 总进度绘制
            canvas.clearRect(0,0,40,40);
            canvas.save();
            canvas.beginPath();
            canvas.strokeStyle = "#ddd";
            canvas.lineWidth = 4;
            canvas.arc(20,20,20,0,2*Math.PI);
            canvas.stroke();
            canvas.closePath();
            canvas.restore();
            // 当前进度绘制
            canvas.save();
            canvas.beginPath();
            canvas.strokeStyle = gradient;
            canvas.lineWidth = 4;
            canvas.arc(20,20,20,0,2*Math.PI*parseFloat(currentTime/duration));
            canvas.stroke();
            canvas.closePath();
            canvas.restore();
        },
        like(){ //收藏
            this.likeState = !this.likeState;
            let newLike = JSON.parse( window.localStorage.getItem('likeSongList'));
            if(newLike){
                if(this.likeState){
                    newLike.push(String(this.$root.musicStore.musicID));
                    newLike = Array.from(new Set(newLike));
                }else{
                    let index = newLike.indexOf(String(this.$root.musicStore.musicID));
                    newLike.splice(index,1);
                }
            }else{
                newLike = [];
                newLike.push(String(this.$root.musicStore.musicID));
            }
            window.localStorage.setItem('likeSongList',JSON.stringify(newLike));
        },
    },
    filters:{ //时间转换
        formatTime(val){
            if(val){
                let h = Math.floor( val/3600 ) < 10 ? "0"+Math.floor( val/3600 ) : Math.floor( val/3600 );
                let m = Math.floor( val%3600/60 ) < 10 ? "0"+Math.floor( val%3600/60 ) : Math.floor( val%3600/60 );
                let s = Math.floor( val%60 ) < 10 ? "0"+Math.floor( val%60 ) : Math.floor( val%60 );
                return `${h}:${m}:${s}`;
            }else{
                return "00:00:00";
            }
        }
    },
    computed:{
        musicIDs(){
            let musicID = this.$root.musicStore.musicID;
            return musicID;
        },
    },
    watch:{
         musicIDs(musicID){
            let likeList = JSON.parse( window.localStorage.getItem('likeSongList'));
            if(likeList){
                this.likeState = false;
                likeList.forEach( item => {
                    if(item == String(musicID)){
                        this.likeState = true;
                        return false;
                    }
                });
            }else{
                this.likeState = false;
            }
            this.$http.get('/lyric?id='+musicID).then(data =>{
                this.lyric = null;
                if(data.data.nolyric || !data.data.lrc) return false;
                if(data.data.lrc.lyric.indexOf('[00:00.000]')==-1) return false;
                let lyricArr = data.data.lrc.lyric.split('\n');
                lyricArr.pop(); //取出匹配到的最后一条
                let reg = /\[(\d+):(\d+\.\d+)\](.*)/; //匹配歌词
                let newLyric = lyricArr.map( item => {
                    if(reg.test(item)){
                        return{
                            time: parseInt(RegExp.$1)*60 + parseFloat(RegExp.$2),
                            lyric: RegExp.$3
                        }
                    }
                });
                this.lyric = newLyric;
            }).catch(err=>{
                console.log(err);
            });
        }
    },   
}
</script>


<style lang="less" scoped>
.player{
    width: 100%;
    position:fixed;
    z-index: 9;
    bottom: 0px;
    background-color: #fff;
    audio{
        height: 50px;
    }
    //播放条
    .playBar{
        display: flex;
        align-items: center;
        padding: 5px 20px;
        color: rgb(41, 36, 36);
        background-color: #FEFEFE;
        border-top: 2px solid #ddd;
        position:relative;
        i{
            &:first-of-type{
                width: 50px;
                height: 50px;
                border-radius: 50%;
                overflow: hidden;
                animation: randImg 5s linear 0s infinite;
                box-shadow: 2px 1px 8px 2px rgba(0, 0, 0, 0.5);
            }
            &:nth-of-type(2){
                flex: 1;
                line-height: 30px;
                text-align: center;
                font-style: normal;
            }
            &:last-of-type{
                width: 50px;
                height: 50px;
                border-radius: 50%;
                canvas{
                    width: 100%;
                }
            }
        }
        .highlight{
            position: absolute;
            right: 27px;
            top: 12px;
            img{
                width: 36px;
                height: 36px;
                background: #ccc;
                border-radius: 50%;
            }
        }
    }
    //播放页
    .fullPage{
        width: 100vw; //vw 把屏幕分100份
        height: 100vh;
        position: relative;
        .bg{
            position: absolute;
            left: 0;
            top: 0;
            height: 100%;
            z-index: -1;
            overflow: hidden;
            filter:blur(20px) brightness(0.5);
            img{
                height: 100vh;
                object-fit: cover; 
                vertical-align: baseline;
            }
        }
        .top{
            width: 100%;
            height: 50px;
            display: flex;
            align-items: center;
            margin-top: 6px;
            .clock{
                display: block;
                width: 32px;
                height: 32px;
                background: url(../assets/imgs/6.png);
            }
            h3{
                flex: 1;
                text-align: center;
                height: 6vh;
                font-size: 16px;
                margin-bottom: 2px;
                color: #FEFEFE;
            }
            .iconfont{
                color: white;
                font-size: 29px;
            }
            
        }
        .songImg{
            width: 300px;
            height: 300px;
            margin: 30px auto;
            border-radius: 50%;
            overflow: hidden;
            animation: randImg 5s linear 0s infinite;
            img{
                width: 300px;
                height: 300px;
            }
        }
        .lyric{
            text-align: center;
            color: white;
            height: 22vh;
            font-size: 14px;
            position: relative;
            overflow: auto;
            transition: all .1s;
            h3{
                display: none;
            }  
            &.lyricFull{
                position: absolute;
                z-index: 999;
                top: 0px;
                width: 100vw;
                height: 100vh;
                background-color:rgba(32, 32, 32, 0.9);
                h3{
                    height: 12vh;
                    line-height: 8vh;
                    margin:5px auto ;
                    display: block;
                }
                div{
                    height: 82vh;
                    padding-bottom: 20px;
                }
            }
            
            div{
                overflow: hidden;
                ul{
                    li{
                        margin-top: 6px;
                        color: #FEFEFE;
                        &.active{
                            color: #f7f329;
                        }
                    }
                }
            }
        }
        .nolyric{
            text-align: center;
            p{
                color: #f7f329;
            }
        }
        .propress_open{
            position: fixed;
            bottom: 80px;
            display: flex;
            justify-content: space-around;
            width: 100vw;
            color: #FEFEFE;
            text-align: center;
            i{
                font-style: normal;
                font-size: 12px;
                width: 20%;
            }
            input{
                width:52%;
            }
        }
        
        .controlBar{
            position: fixed;
            bottom: 0px;
            width: 100vw;
            height: 70px;
            display: flex;
            justify-content: space-c;
            align-items: center;
            background: rgba(255, 254, 254,0.1);
            span{
                text-align: center;
                flex: 1;
                color: #fff;
                font-size: 26px;
                cursor: pointer;
                &:nth-of-type(3){
                    font-size: 50px;
                }
            }
            .like{
                color: red;
            }
        }
        .playList{
            width: 96vw;
            height: 62vh;
            border-radius: 10px;
            overflow-y: auto;
            z-index: 20;
            position: fixed;
            left: 2vw;
            top: 76px;
            box-shadow: 0px 0px 10px #ddd;
            background-color: rgba(255, 255, 255, 0.4);
            transition: all 1s;
            transform:translateX(150%);
            &.active{
                transform: translateX(0%);
            }
            p{
                height: 6vh;
                padding: 10px 10px ;
                span{
                    font-size: 18px;;
                    float: right;
                }
            }
            ul{
                li{
                    padding: 4px 10px;
                    position: relative;
                    width: 100%;
                    display: -webkit-box;
                    -webkit-line-clamp: 1;
                    -webkit-box-orient: vertical;
                    overflow: hidden;
                    justify-content: space-between;
                    span{
                        color: black !important;
                    }
                }
            }
        }
         
    }
}
.lyricTitle{
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    overflow: hidden;
}
  

@keyframes randImg {
    0%{
        transform: rotate(0deg);
    }
    100%{
        transform: rotate(360deg);
    }
}
</style>