<template>
  <div id="app">
    <TopNav></TopNav>
    <Play ref="playClose"></Play>
    <div :style="{
      paddingTop:$route.meta.showNav?'90px':''
    }"></div>
    <router-view></router-view>
    <div :style="{paddingBottom:this.$root.playBar?'64px':''}"></div>
  </div>
</template>
<script>

import TopNav from './components/TopNav'
import Play from './components/Play'
export default {
  name:"App",
  data(){
    return{
      
    }
  },
  methods:{
  },
  components:{
    TopNav,
    Play
  },
  watch:{
    '$route' (to) { //监听路由是否变化
        if(to.path.indexOf('/mv') != -1){
          this.$refs.playClose.pauseThis();
          this.$root.playBar = false;
          this.$root.isplay= false;
        }
    },
  },
}
</script>
<style lang="less" scoped>
#app{
  background-color: #FCFCFD;
}
</style>
